<template>
	<div v-if="serie">
		<div class="">
			<h3 class="uk-text-large uk-margin-remove">{{serie.chapters.length}} {{'CHAPTERS' | translate}}</h3>
			<!-- <div class="uk-flex uk-flex-middle">
				<ul class="it-chapter-view uk-subnav uk-margin-remove" uk-switcher="connect: .chapter-sort-class">
					<li>
						<a href="#" class="uk-button uk-button-small"><span uk-icon="icon: list"></span> <span class="uk-visible@s">{{'VIEW_LIST' | translate}}</span></a>
