<template>
	<SeriesList v-if="series" :series="series || []" :classProp="'uk-child-width-1-5@l'"></SeriesList>
</template>

<script>
	import SeriesList from "../components/SeriesList.vue";
import { SERIES_RELATED_SERIES_RELATED }  from '../constants/graphql';

	export default {
		name: "SeriesRelated",
		data: function() {
			return {
				series: null,
				api_url: process.env.VUE_APP_STRAPI_API_URL
			};
		},
		props: {
			serie: Object
		},
		components: {
			SeriesList,
		},
		apollo: {
			series: {
				query: SERIES_RELATED_SERIES_RELATED,
				variables() {
					return {
						id: [this.serie.id],
						genreIds: this.serie.genres.map(x => x.id)
					};
				},
				skip () {
					return !this.serie
				},
			},
		}
	};
</script>